.details {
  &__wraper {
    display: grid;
    row-gap: 40px;
    margin-bottom: 40px;

    @include onTablet {
      row-gap: 48px;
      margin-bottom: 48px;
    }
  }

  &__button {
    @include onTablet {
      @include pageGrid;
      grid-column: 2 / -2;
    }
    @include onDesktop {
      grid-column: 5 / 9;
    }
  }
}

.detail {
  &__photos {
    @include pageGrid;
    margin-bottom: 16px;
  }

  &__link {
    position: relative;
    &--wide {
      @include onTablet {
        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;

      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }
    :hover {
      @include hover(transform, scale(1.05));
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__descriptions {
    @include onDesktop {
      @include pageGrid;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 8px;
    font-size: 24px;
    font-weight: 700;
    line-height: 140%;
    @include onDesktop {
      grid-column: span 12;
    }

  }

  &__text {
    margin: 0;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
    @include onDesktop {
      grid-column: span 8;
    }
  }
}
